import React, { useState } from "react";
import "./Navbar.css";
import { Link } from "react-router-dom";

function Navbar() {
  const [click, setClick] = useState(false);

  const closeMobileMenu = () => setClick(false);
  return (
    <nav className="navbar">
      <div className="nav-icon" onClick={(e) => setClick(!click)}>
        <i className={click ? "fas fa-times" : "fas fa-bars"} />
      </div>
      <div className="navbar-logo">Ullas Kunder</div>
      <ul className={click ? "navbar-items active" : "navbar-items"}>
        <Link to="/" className="navbar-items-link" onClick={closeMobileMenu}>
          Home
        </Link>
        <Link
          to="/about"
          className="navbar-items-link"
          onClick={closeMobileMenu}
        >
          About
        </Link>
        <Link
          to="/qualifications"
          className="navbar-items-link"
          onClick={closeMobileMenu}
        >
          Qualifications
        </Link>
        {/* <Link to="/achievements" className="navbar-items-link">
          Achievements
        </Link> */}
        <Link
          to="/skills"
          className="navbar-items-link"
          onClick={closeMobileMenu}
        >
          Skills
        </Link>
        <Link
          to="/contact"
          className="navbar-items-link"
          onClick={closeMobileMenu}
        >
          Contact
        </Link>
      </ul>
    </nav>
  );
}

export default Navbar;
