import React from "react";
import "./Skills.css";
import Footer from "./ui/Footer";
import flutterImage from "../images/flutter.png";
import reactjsImage from "../images/reactjs.png";
import mongodbImage from "../images/mongodb.png";
import mysqlImage from "../images/mysql.png";
import nodejsImage from "../images/nodejs.png";
import androidstudioImage from "../images/androidstudio.png";
import adobexdImage from "../images/adobexd.png";
import illustratorImage from "../images/illustrator.png";
import framerImage from "../images/framer.png";
import digitaloceanImage from "../images/digitalocean.png";
import hostingerImage from "../images/hostinger.png";
import firebaseImage from "../images/firebase.png";
import canvaImage from "../images/canva.png";
import unityImage from "../images/unity.png";
import javascriptImage from "../images/javascript.png";
import dartImage from "../images/dart.png";
import javaImage from "../images/java.png";
import pythonImage from "../images/python.png";
import phpImage from "../images/php.png";
import cppImage from "../images/cpp.png";
import cImage from "../images/c.png";
import csImage from "../images/cs.png";
import htmlImage from "../images/html.png";
import cssImage from "../images/css.png";

function SkillCard({ image, name }) {
  return (
    <div className="skills-card">
      <img src={image} alt="Skill" className="skills-image" />
      <p className="skills-image-name">{name}</p>
    </div>
  );
}

function Skills() {
  return (
    <>
      <div className="skills-container">
        <div className="skills-list-container">
          <h2 className="skills-name">Technology Stack</h2>
          <hr />
          <div className="skills-horizontal-list">
            <SkillCard image={androidstudioImage} name="Android Studio" />
            <SkillCard image={flutterImage} name="Flutter" />
            <SkillCard image={reactjsImage} name="React JS" />
            <SkillCard image={mysqlImage} name="MySQL" />
            <SkillCard image={mongodbImage} name="Mongo DB" />
            <SkillCard image={nodejsImage} name="Node JS" />
            <SkillCard image={firebaseImage} name="Firebase" />
            <SkillCard image={hostingerImage} name="Hostinger" />
            <SkillCard image={digitaloceanImage} name="Digital Ocean" />
            <SkillCard image={unityImage} name="Unity" />
            <SkillCard image={illustratorImage} name="Adobe Illustrator" />
            <SkillCard image={adobexdImage} name="Adobe XD" />
            <SkillCard image={canvaImage} name="Canva" />
            <SkillCard image={framerImage} name="Framer" />
          </div>
        </div>
        <div className="skills-list-container">
          <h2 className="skills-name">Programming Languages</h2>
          <hr />
          <div className="skills-horizontal-list">
            <SkillCard image={cImage} name="C" />
            <SkillCard image={cppImage} name="C++" />
            <SkillCard image={csImage} name="C#" />
            <SkillCard image={dartImage} name="Dart" />
            <SkillCard image={htmlImage} name="HTML" />
            <SkillCard image={cssImage} name="CSS" />
            <SkillCard image={javascriptImage} name="JavaScript" />
            <SkillCard image={phpImage} name="PHP" />
            <SkillCard image={javaImage} name="Java" />
            <SkillCard image={pythonImage} name="Python" />
          </div>
        </div>
      </div>
      <div className="spacer"></div>
      <Footer />
    </>
  );
}

export default Skills;
