import React, { useState, useEffect } from "react";
import "./About.css";
import Lottie from "react-lottie";
import animationData from "../lotties/about.json";
import Footer from "./ui/Footer";

function About() {
  const [height, setHeight] = useState(400);
  const [width, setWidth] = useState(400);

  useEffect(() => {
    if (window.screen.width < 800) {
      setHeight(300);
      setWidth(300);
    }
  }, []);

  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: animationData,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };
  return (
    <>
      <div className="about-container">
        <div className="about-lottie-container">
          <Lottie options={defaultOptions} height={height} width={width} />
        </div>
        <div className="about-text-container">
          <h2 className="text-container-heading">Hello, I'm Ullas</h2>
          <hr />

          <p className="text-container-paragraph">
            Hello, I am Ullas Kunder a 20 year old Full Stack Web developer and
            mobile application developer from the Temple Town Udupi Karnataka,
            India. Creative and Team player who is always ready to learn new
            Technologies and love to contribute to the organization and Make In
            India.
          </p>
          <p className="text-container-paragraph">
            I'm a third year Computer Science and Engineering student in Shri
            Madhwa Vadiraja Institute of Technology and Management Bantakal.I
            have been working as freelance software engineer from past few
            years.
          </p>
          <p className="text-container-paragraph">
            My primary interest is mobile app development and backend engineer
            and also work on Full Stack web development. I also like developing
            games in my free time. TIll date we have worked on various app, web
            and game development projects
          </p>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default About;
