import React from "react";
import "./Qualifications.css";
import Footer from "./ui/Footer";
import SchoolImage from "../images/school.png";
import CollegeImage from "../images/college.png";
import EngineeringImage from "../images/engineering.png";
import FreelanceImage from "../images/freelance.png";
import WebDevelopmentImage from "../images/webdevelopment.png";
import AppDevelopmentImage from "../images/appdevelopment.png";

function QualificationCard({
  image,
  name,
  schoolStandard,
  percentage,
  duration,
  className,
}) {
  return (
    <div className={className}>
      <img src={image} alt="LOGO" className="qualifications-card-image" />

      <p className="qualification-school-name">{name}</p>
      <hr />
      <p className="qualification-class">{schoolStandard}</p>
      <hr />
      <p className="qualification-percentage">{percentage}</p>
      <hr />
      <p className="qualification-duration">{duration}</p>
    </div>
  );
}

function Qualifications() {
  return (
    <>
      <div className="qualifications-container">
        <div className="qualifications-text-container">
          <h2 className="qualifications-firstname">Educational</h2>
          <h4 className="qualifications-lastname">Qualifications</h4>
        </div>

        <div className="qualifications-card-container">
          <QualificationCard
            image={SchoolImage}
            name="MHS Kallianpura"
            schoolStandard="Class 10"
            percentage="90.56%"
            duration="2012-2015"
            className="qualifications-card"
          />
          <QualificationCard
            image={CollegeImage}
            name="Shamili PUC Kidiyoor"
            schoolStandard="Class 12"
            percentage="85.56%"
            duration="2015-2017"
            className="qualifications-card middle"
          />
          <QualificationCard
            image={EngineeringImage}
            name="SMVITM Bantakal"
            schoolStandard="B.E"
            percentage="8.9 CGPA"
            duration="2017-2021"
            className="qualifications-card"
          />
        </div>
      </div>
      <div className="qualifications-container mobile-container">
        <div className="qualifications-card-container">
          <QualificationCard
            image={WebDevelopmentImage}
            name="Arya BrightCare"
            schoolStandard="Intern Full Stack Engineer"
            percentage="3 Months"
            duration="2020"
            className="qualifications-card"
          />
          <QualificationCard
            image={AppDevelopmentImage}
            name="Dream Soft Innovation Pvt. Ltd."
            schoolStandard="Intern App & Web Developer"
            percentage="4 Months"
            duration="2020"
            className="qualifications-card middle"
          />
          <QualificationCard
            image={FreelanceImage}
            name="Freelance Software Engineer"
            schoolStandard="Full Stack Engineer"
            percentage="2 Years"
            duration="2018 -"
            className="qualifications-card"
          />
        </div>
        <div className="qualifications-text-container">
          <h2 className="qualifications-firstname">Work</h2>
          <h4 className="qualifications-lastname">Expereince</h4>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default Qualifications;
