import React, { useState, useEffect } from "react";
import "./Home.css";
import Lottie from "react-lottie";
import animationData from "../lotties/home.json";
import Footer from "./ui/Footer";

function Home() {
  const [height, setHeight] = useState(400);
  const [width, setWidth] = useState(400);

  useEffect(() => {
    if (window.screen.width < 800) {
      setHeight(300);
      setWidth(300);
    }
  }, []);

  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: animationData,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };

  return (
    <>
      <div className="home-container">
        <div className="home-container-name-container">
          <h2 className="name-container-firstname">ULLAS</h2>
          <h4 className="name-container-lastname">KUNDER</h4>
          <hr />
          <h5 className="name-container-designation">SOFTWARE ENGINEER</h5>
        </div>
        <div className="home-container-lottie-container">
          <Lottie options={defaultOptions} height={height} width={width} />
        </div>
      </div>
      <Footer />
    </>
  );
}

export default Home;
